.details {
    display: none;
}

.visible {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: auto;
    height: 600px;
    width: 400px;
    border: 1px solid black;
    padding: 30px;
    margin-top: 100px;
    position: fixed;
    top: 10%;
    right: 13%;
    margin-top: 100px;
}

.visible img {
    width: 200px;
    height: 200px;
}

.visible h2 {
    margin: 10px 0;
}

table {
    border-collapse: collapse;
    width: 100%;
}

th,
td {
    border: 1px solid black;
    padding: 8px;
    text-align: center;
}

th {
    background-color: #ddd;
}

tr:nth-child(even) {
    background-color: #f2f2f2;
}

tr:hover {
    background-color: #ddd;
}