.card-pokemon {
    text-align: center;
    border: 1px solid black;
    padding: 30px;
    margin: 10px;
    cursor: pointer;
}

.card-pokemon:hover {
    transform: scale(1.1);
}

.card-pokemon img {
    height: 200px;
    width: 200px;
    object-fit: contain;
    transition: transform 0.2s;
}

.card-pokemon h2 {
    margin-top: 10px;
}

.type-pokemon {
    text-align: start;
    font-size: 20px;
    display: flex;
}

.type-pokemon div {
    margin-left: 10px;
    border-radius: 10px;
    padding: 3px;
}

.rock {
    background-color: rgb(148, 81, 81);
}

.ghost {
    background-color: rgb(247, 247, 247);
}

.electric {
    background-color: rgb(255, 255, 161);
}

.bug {
    background-color: #F6D6A7;
}

.poison {
    background-color: #dd86ff;
}

.normal {
    background-color: #F4F4F4;
}

.fairy {
    background-color: rgba(255, 192, 203, 0.863);
}

.fire {
    background-color: #FBE3DF;
}

.grass {
    background-color: #4eec49;
}

.water {
    background-color: #0095ff;
}

.flying {
    background-color: #698df0;
}