* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.Load-data {
    text-align: center;
    font-size: 72px;
    margin-top: 400px;
}

.App p {
    text-align: center;
    font-size: 56px;
    border: 1px solid black;
    margin: 30px 40%;
}

.select-type{
    text-align: center;
    font-size: 24px;
}

.select-type select {
    width: 200px;
}

.select-type select option{
    font-size: large;
    width: 700px;
}

.main {
    display: flex;
}

.list-pokemon {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    width: 50%;
}

.details-pokemon {
    width: 50%;
}

.btn-load-more {
    width: 500px;
    margin: 20px 250px;
    background-color: rgb(0, 132, 255);
    border: none;
    font-size: 24px;
    padding: 5px;
    color: white;
    cursor: pointer;
    border-radius: 15px;
    width: 100%;
}